const bodyScrollLock = require("body-scroll-lock");
const disableBodyScroll = bodyScrollLock.disableBodyScroll;
const enableBodyScroll = bodyScrollLock.enableBodyScroll;

import SmoothScroll from "smooth-scroll";
import MarkerClusterer from "@google/markerclustererplus";
var mapSettings = require("./map-settings.js");
const initExpandables = () => {
  $(".expandable-toggle").on("click", function(e) {
    e.preventDefault();
    $(this)
      .parents(".expandable")
      .toggleClass("open");
  });
};

let map,
  bounds,
  geocoder,
  markerCluster,
  markersInView = [],
  locations = [],
  currentInfoWindow,
  defaultZoom = 15;

const initMap = () => {
  var input = document.getElementById("location");
  var autocomplete = new google.maps.places.Autocomplete(input);
  autocomplete.setFields(["address_components", "geometry", "icon", "name"]);

  autocomplete.addListener("place_changed", function() {
    $("#location")
      .parents("form")
      .submit();
  });

  $("#locator-form").on("submit", function(e) {
    e.preventDefault();
    geocoder = new google.maps.Geocoder();
    let address = $("#location").val();
    geocoder.geocode({ address: address }, function(results, status) {
      if (status == google.maps.GeocoderStatus.OK) {
        var searchBounds = new google.maps.LatLngBounds();
        var latlng = new google.maps.LatLng(
          results[0].geometry.location.lat(),
          results[0].geometry.location.lng()
        );

        searchBounds.extend(latlng);
        map.setCenter(searchBounds.getCenter());
        map.setZoom(13);
      } else {
        console.log(
          "Geocode was not successful for the following reason: " + status
        );
      }
    });
  });

  $('input[name="location_type"]').on("change", function() {
    updateMapResults();
  });

  $("body").on("click", "[data-location-id]", function(e) {
    e.preventDefault();
    let locationId = $(this).attr("data-location-id");
    map.markers.forEach(function(marker) {
      if (marker.locationId == locationId) {
        map.setZoom(17);
        map.panTo(marker.position);
        new google.maps.event.trigger(marker, "click");
        var scroll = new SmoothScroll();
        scroll.animateScroll($("#top")[0]);
      }
    });
  });

  const mapBoundsChanged = function() {
    markersInView = [];
    if (map.getBounds()) {
      for (var i = 0; i < map.markers.length; i++) {
        if (map.getBounds().contains(map.markers[i].getPosition())) {
          if (typeof locations[i] !== "undefined") {
            markersInView.push(locations[i]);
          }
        }
      }
      if (
        typeof map.markerClusterer !== "undefined" &&
        map.markerClusterer !== null
      ) {
        map.markerClusterer.repaint();
      }
      updateTextResults(markersInView);
    }
  };

  const updateTextResults = function(markersInView) {
    $(".location-text-results").html("");
    let ctaClass = "dark";
    if ($("body").hasClass("storytellers-site")) {
      ctaClass = "white";
    }
    $(markersInView).each(function(idx, location) {
      let locationMarkup = `
        <div class="col-10 col-lg-3 link-block" data-category="${location.category}">
        <div class="text wysiwyg-text h-100 d-flex flex-column">
          <p class="type--b-em"><a href="#" data-location-id="${location.id}">${location.name}</a></p>
          <p class="wysiwyg-content">
            ${location.address}<br>
            ${location.phone}
          </p>
          <p class="cta mt-auto">
            <a href="${location.url}" target="_blank" class="cta caret-link type--cta ${ctaClass}">Visit Website<i class="caret"></i></a>
          </p>
        </div>
      </div>
      `;
      $(".location-text-results").append(locationMarkup);
    });
  };

  const updateMapResults = function() {
    let selectedType = $('input[name="location_type"]:checked').val();
    if ($(".location-text-results[data-show]").length) {
      $(".location-text-results[data-show]").attr("data-show", selectedType);
    }
    map.markerClusterer.setIgnoreHidden(true);
    for (var i = 0; i < map.markers.length; i++) {
      map.markers[i].setVisible(false);
    }
    map.markerClusterer.repaint();
    for (var i = 0; i < map.markers.length; i++) {
      if (map.markers[i].locationType == selectedType) {
        map.markers[i].setVisible(true);
      }
    }

    map.markerClusterer.repaint();

    mapBoundsChanged();
  };

  /*
   *  new_map
   *
   *  This function will render a Google Map onto the selected jQuery element
   *
   *  @type function
   *  @date 8/11/2013
   *  @since  4.3.0
   *
   *  @param  $el (jQuery element)
   *  @return n/a
   */

  function new_map($el) {
    let mapStyles = mapSettings.styles,
      clusterImage = "";

    // vars
    var args = {
      zoom: defaultZoom,
      center: new google.maps.LatLng(34.09523, -118.3727),
      mapTypeId: google.maps.MapTypeId.ROADMAP,
      styles: mapSettings.styles,
      panControl: false,
      zoomControl: true,
      //draggable: !("ontouchend" in document),
      zoomControlOptions: {
        style: google.maps.ZoomControlStyle.SMALL,
        position: google.maps.ControlPosition.RIGHT_BOTTOM,
      },
      scrollwheel: false,
      mapTypeControl: false,
      overviewMapControl: false,
      fullscreenControl: false,
      overviewMapControlOptions: {
        opened: false,
      },
      streetViewControl: false,
      scaleControl: false,
    };

    // create map
    var map = new google.maps.Map($el[0], args);

    // add a markers reference
    map.markers = [];

    // add markers
    // get markers and add
    // add pins
    var storeUrl =
      "https://storemapper-herokuapp-com.global.ssl.fastly.net/api/users/5340/stores.js";
    $.getJSON(storeUrl, function(json) {
      $(json.stores).each(function(idx, store) {
        locations.push(store);
        add_marker(store, map);
      });

      var clusterStyles = [
        {
          url: $(".map").attr("data-cluster-icon"),
          textColor: "white",
          textSize: 16,
          height: 40,
          width: 40,
          textLineHeight: 40,
          backgroundPosition: "0 0 ",
        },
      ];
      if (map.markers.length > 1) {
        map.markerClusterer = new MarkerClusterer(map, map.markers, {
          styles: clusterStyles,
          imagePath:
            "https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/m",
        });

        updateMapResults();
      }

      google.maps.event.addListener(map, "bounds_changed", function() {
        mapBoundsChanged();
      });

      center_map(map);
    });

    google.maps.event.addDomListener(window, "resize", function() {
      center_map(map);
    });
    // return
    return map;
  }

  /*
   *  add_marker
   *
   *  This function will add a marker to the selected Google Map
   *
   *  @type function
   *  @date 8/11/2013
   *  @since  4.3.0
   *
   *  @param  $marker (jQuery element)
   *  @param  map (Google Map object)
   *  @return n/a
   */
  var openInfoWindow = null;
  var markerCollection;

  function add_marker(location, map) {
    let markerColor = "#000000";

    // var
    var latlng = new google.maps.LatLng(location.latitude, location.longitude);

    // create marker
    var marker = new google.maps.Marker({
      position: latlng,
      map: map,
      locationId: location.id,
      locationType: location.category,
      optimized: true,
      icon: {
        url: $(".map").attr("data-marker-icon"),
        anchor: new google.maps.Point(0, 16.5),
        size: new google.maps.Size(33, 40),
      },
    });

    // add to array
    map.markers.push(marker);

    // if marker contains HTML, add it to an infoWindow
    var infoWindowContent = `
    <h3>${location.name}</h3>
    <p>${location.address}</p>
    <p>${location.category}</p>
    <p><a href="${location.url}" target="_blank">${location.url}</a></p>
    `;
    if (infoWindowContent) {
      // create info window
      var infowindow = new google.maps.InfoWindow({
        content: infoWindowContent,
        maxWidth: 400,
      });

      // show info window when marker is clicked
      google.maps.event.addListener(marker, "click", function() {
        if (openInfoWindow) {
          openInfoWindow.close();
        }
        openInfoWindow = infowindow;
        infowindow.open(map, marker);
      });
    }
  }

  /*
   *  center_map
   *
   *  This function will center the map, showing all markers attached to this map
   *
   *  @type function
   *  @date 8/11/2013
   *  @since  4.3.0
   *
   *  @param  map (Google Map object)
   *  @return n/a
   */

  function center_map(map) {
    // vars
    bounds = new google.maps.LatLngBounds();

    // loop through all markers and create bounds
    $.each(map.markers, function(i, marker) {
      var latlng = new google.maps.LatLng(
        marker.position.lat(),
        marker.position.lng()
      );

      bounds.extend(latlng);
    });

    // only 1 marker?
    if (map.markers.length == 1) {
      // set center of map
      map.setCenter(bounds.getCenter());
      map.setZoom(16);
    } else {
      // fit to bounds
      map.fitBounds(bounds);
    }
  }
  if ($(".locator-map-container").length) {
    if ($(window).width() < 992) {
      defaultZoom = 14;
    }

    map = new_map($(".locator-map-container .map"));
  }
};

let lastScrollTop = 0,
  st = 0,
  scrollDirection = false;

const updateNav = () => {
  var scrollPercent =
    (100 * $(window).scrollTop()) / ($(document).height() - $(window).height());
  if (scrollPercent > 0) {
    $("body").addClass("has-scrolled");
  } else {
    $("body").removeClass("has-scrolled");
  }

  if (scrollDirection == "up") {
    $("body")
      .removeClass("scroll-direction-down")
      .addClass("scroll-direction-up");
  } else {
    if (scrollPercent > 0) {
      $("body")
        .removeClass("scroll-direction-up")
        .addClass("scroll-direction-down");
    }
  }

  lastScrollTop = st;
};

const setupGlobalScrollEvents = () => {
  $(window)
    .on("scroll", function() {
      st = $(window).scrollTop();
      if (st < lastScrollTop) {
        scrollDirection = "up";
      } else {
        scrollDirection = "down";
      }
      lastScrollTop = st;
      requestAnimationFrame(updateNav);
    })
    .trigger("scroll");
};

const initNav = () => {
  $(".bars").on("touchstart click", function(e) {
    e.preventDefault();
    if ($("body").hasClass("site-header--open")) {
      // unlock scrolling
      $("body").removeClass("site-header--open");
      bodyScrollLock.clearAllBodyScrollLocks();
    } else {
      // lock scrolling
      $("body").addClass("site-header--open");
      disableBodyScroll($(".mobile-nav--links")[0]);
    }
  });

  $(".hover-images").each(function(idx, elem) {
    $(elem)
      .find(".hover-image:first")
      .addClass("active");
  });

  $(".hover-image").each(function(idx, elem) {
    let href = $(this).attr("data-link-url");
    $('.primary-nav--links a[href="' + href + '"]').on("mouseover", function(
      e
    ) {
      $(this)
        .parents(".child-menu")
        .find(".hover-image.active")
        .removeClass("active");
      $(elem).addClass("active");
    });
  });

  $(".section--post-listing .load-more, .section--links .load-more").on(
    "click",
    function(e) {
      e.preventDefault();
      $(this)
        .parents(".load-more-container")
        .find(".will-reveal")
        .removeClass("d-none");

      $(this)
        .parents(".row")
        .remove();
    }
  );
};

const initHero = () => {
  $(".hero .slides").each(function(idx, elem) {
    if ($(elem).find(".hero-slide").length > 1) {
      $(elem).flickity({
        pageDots: false,
        prevNextButtons: false,
        wrapAround: true,
      });
    }
  });

  $(".block-carousel").each(function(idx, elem) {
    if ($(elem).find(".slide").length > 3) {
      $(elem).flickity({
        wrapAround: true,
        pageDots: false,
        prevNextButtons: true,
        cellAlign: "right",
      });
    }
  });
};

const initProductDetails = () => {
  $(".product-details-carousel-images").each(function(idx, elem) {
    if ($(elem).find(".slide").length > 1) {
      var $carousel = $(elem).flickity({
        pageDots: false,
        prevNextButtons: false,
        wrapAround: true,
        adaptiveHeight: false,
        fade: true,
      });

      $(window)
        .on("resize", function() {
          $(elem).flickity("resize");
        })
        .trigger("resize");

      $(".product-details-carousel-nav .thumb").on("click", function(e) {
        e.preventDefault();
        let slideTarget = $(this).attr("data-slide-target");
        $carousel.flickity("select", slideTarget);
      });
    }
  });

  $("#locationSelect").on("change", function(e) {
    let val = $(this).val(),
      target = $('#locationSelect option[value="' + val + '"]').attr(
        "data-target"
      );
    if (target == "speakeasy") {
      $("#speakeasy-link").removeClass("d-none");
      $("#thirstie-link").addClass("d-none");
    }
    if (target == "thirstie") {
      $("#speakeasy-link").addClass("d-none");
      $("#thirstie-link").removeClass("d-none");
    }
    if (val !== "" && val !== "Select your location") {
      $(this)
        .parents(".cta-link")
        .addClass("has-location");
    } else {
      $(this)
        .parents(".cta-link")
        .removeClass("has-location");
    }
  });
};

