const initPostDetails = () => {
  $(".post-body img").each(function(idx, elem) {
    $(elem).wrap('<div class="image"></div>');
    if ($(elem).attr("alt").length) {
      let caption = $(elem).attr("alt");
      $(elem)
        .parents(".image")
        .addClass("with-caption")
        .append('<div class="caption">' + caption + "</div>");
    }
  });

  $(".post-body .image").unwrap("p");
};

function fadeInPage() {
  if (!window.AnimationEvent) {
    return;
  }
  var fader = document.getElementById("fader");
  if (fader) {
    fader.classList.add("fade-out");
  }
}

document.addEventListener("DOMContentLoaded", function() {
  if (!window.AnimationEvent) {
    return;
  }

  var anchors = document.getElementsByTagName("a");

  for (var idx = 0; idx < anchors.length; idx += 1) {
    // console.log(anchors[idx].hostname);
    // console.log(window.location.hostname);

    if (anchors[idx].hostname !== window.location.hostname) {
      continue;
    }

    if (anchors[idx].classList.contains("no-fade") == false) {
      anchors[idx].addEventListener("click", function(event) {
        var fader = document.getElementById("fader"),
          anchor = event.currentTarget;

        var listener = function() {
          window.location = anchor.href;
          fader.removeEventListener("animationend", listener);
        };
        fader.addEventListener("animationend", listener);

        event.preventDefault();
        fader.classList.add("fade-in");
      });
    }
  }
});

window.addEventListener("pageshow", function(event) {
  if (!event.persisted) {
    return;
  }
  var fader = document.getElementById("fader");
  fader.classList.remove("fade-in");
});

fadeInPage();

$(document).ready(function() {
  setupGlobalScrollEvents();
  initNav();
  initHero();
  initExpandables();
  initProductDetails();
  initGrids();
  initPostDetails();
  if ($(".map").length) {
    initMap();
  }

  var scroll = new SmoothScroll("a[data-scroll]");

  $(document).on("lazyloaded", function(e) {
    $(window).trigger("resize");
  });
});
